  .container {
    text-align: center;
    margin-top: 10px;
    border-radius: 25px;
    background-color: rgba(22, 19, 19, 0.849);
    color: white;
  }

  input, select, textarea{
    color: #26a69a
}

textarea:focus, input:focus {
    color: #26a69a
}

  #transition-modal-title,
  #transition-modal-description {
    text-align: center;
  }

 .form-control {
    text-align: center;
  }

  [type="checkbox"]+span:not(.lever) {
    display: inline;
    display: initial;
    margin-left: 10px;
  }

  img.responsive-img {
    margin-top: 10px;
  }
  
  label {
    font-size: 1rem;
    color: white;
  }

  textarea {
    text-align: center;
  }

  .waves-effect.waves-teal.btn-flat {
    color: white;
  }
/* Adds a custom color for the header background */
nav {
    background-color: rgb(125, 141, 191);
  }

/* This changes the color 'Login' button in header */
nav ul a.btn-flat {
  color: white;
  font-size: 2rem;
}


nav ul a {
    font-family: 'Anton', sans-serif;
    font-style: italic;
    margin-right: 45px;
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 1.50px; 
}

/* These apply all the styles to make our logo 'pop' */
nav .brand-logo {
    font-family: 'Anton', sans-serif;
    font-style: italic;
    font-size: 3rem;
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 2.00px; 
}

/* Ensures proper placement and size of the travelRush logo!*/
img.responsive-img, video.responsive-video {
    justify-content: left;
    max-width: 40%;
    margin-left: 10px;
    padding-left: 20px;
}

.red {
    background-color: rgb(51, 51, 153) !important;
  }
.card {
    width:295px;
    display: block;
    /* margin: 60px auto; */
    float: left;
    box-shadow: 10px 5px 40px 20px 
    darken(#341cac, 5%);
    -webkit-transition: .25s;
    transition: .25s;
    margin-left: 50px;
}

.card-content {
    text-align: center;
}

.card-action {
    text-align: center;
}

#faves-btn {
border:none;
}

